import * as React from "react";
import { hot } from "react-hot-loader";

export class MouseGame extends React.Component {
    public state = {
        connected: false
    }

    public componentDidMount() {
        const scriptsio = document.createElement("script");
        scriptsio.src = "https://cdn.socket.io/3.1.3/socket.io.min.js";
        document.body.appendChild(scriptsio);

        scriptsio.onload = function() {

            const scriptts = document.createElement("script");
            scriptts.src = "https://unpkg.com/typescript@latest/lib/typescriptServices.js";
            document.body.appendChild(scriptts);
    
            scriptts.onload = function() {

                fetch('/assets/mousegame/script.ts').then(x => x.text()).then((result) => {
                    const tsCode = result;
                    const jsCode = window['ts'].transpile(tsCode);
                    eval(`window['dmdasscserverurl'] = 'http://localhost:8080';`);
                    eval(jsCode);
                })

               

                
            }

            
        }
    }

    public render() {
        return (
            <>
                
            </>
        );
    }
}

export default hot(module)(MouseGame);
